import React, { useEffect } from "react";
import { useTransition, animated } from "react-spring";
import "./styles.css";

const AnimatedImage = ({ imageSrc }) => {
  const imageTransition = useTransition(imageSrc, (item) => item, {
    from: {
      opacity: 0,
      position: "absolute",
      transform: "translate3d(0,100%,0)"
    },
    enter: {
      opacity: 1,
      transform: "translate3d(0,0,0)"
    },
    leave: {
      opacity: 0,
      transform: "translate3d(0,-100%,0)"
    }
  });

  return (
    <div style={{ margin: "30px" }}>
      {imageTransition.map(({ item, props, key }) => (
        <animated.img style={props} key={key} src={item}></animated.img>
      ))}
    </div>
  );
};

export default function App() {
  const images = ["/img/stack.png", "/img/github.png"];
  const [index, setIndex] = React.useState(0);
  useEffect(() => {
    const timer = setInterval(() => {
      setIndex((ind) => {
        return (ind + 1) % 2;
      });
    }, 3000);
    return () => {
      clearInterval(timer);
    };
  }, []);
  return (
    <div className="App">
      <h1>Animated image component</h1>
      <h2>it changes the image content with animation</h2>
      <br />
      <AnimatedImage imageSrc={images[index]} />
    </div>
  );
}
